<template>
    <el-container class="hub-wrapper">
        <el-header height="70px">
            <span class="title">MarkdownHub</span>
        </el-header>
        <el-container>
            <el-aside class="scrollbar" width="350px">
                <el-menu @select="onSelectChange">
                    <el-submenu v-for="doc of docs" :index="doc.title" :key="doc.title">
                        <template slot="title">
                            <span style="font-size: 18px">{{doc.title}}</span>
                        </template>
                        <template v-for="sub of doc.children">
                            <el-submenu v-if="sub.children" :index="sub.title" :key="sub.title">
                                <template slot="title">
                                    <span style="font-size: 16px">{{sub.title}}</span>
                                </template>
                                <el-menu-item :index="item.sha" v-for="item of sub.children" :key="item.title">
                                    <span slot="title">{{item.title}}</span>
                                </el-menu-item>
                            </el-submenu>
                            <el-menu-item v-else :index="sub.sha" :key="sub.title">
                                <span slot="title">{{sub.title}}</span>
                            </el-menu-item>
                        </template>
                    </el-submenu>
                </el-menu>
            </el-aside>
            <el-main class="scrollbar">
                <transition name="fade">
                    <div v-if="html" class="markdown-body" v-html="html"></div>
                </transition>
            </el-main>
        </el-container>
    </el-container>
</template>

<script>
    import {mapState} from 'vuex';
    import marked from 'marked';
    import highlight from 'highlight.js';

    marked.setOptions({
        renderer: new marked.Renderer(),
        highlight: function (code) {
            return highlight.highlightAuto(code).value;
        },
        pedantic: false,
        gfm: true,
        tables: true,
        breaks: false,
        sanitize: false,
        smartLists: true,
        smartypants: false,
        xhtml: false
    });

    export default {
        name: "Hub",
        created: function () {
            if (this.docs.length === 0) {
                this.repos.forEach(repo => {
                    this.$store.dispatch("getTree", repo).then();
                });
            }
        },
        computed: {
            ...mapState(["repos", 'docs', 'markdownMap'])
        },
        methods: {
            onSelectChange: function (sha) {
                if (this.current === sha) {
                    return;
                }
                this.current = sha;
                this.html = null;
                const markdown = this.markdownMap.get(sha);
                if (!markdown.content) {
                    const repo = markdown.repo;
                    this.$store.dispatch('getBlobs', {repo, sha})
                        .then(res => {
                            this.html = marked(res);
                            markdown.content = res;
                        });
                } else {
                    this.html = marked(markdown.content);
                }

            }
        },
        data: function () {
            return {
                html: null,
                current: null
            }
        }
    }
</script>

<style lang="less" scoped>
    @import "../assets/github-markdown.css";
    @import "../../node_modules/highlight.js/styles/atelier-estuary-light.css";

    .hub-wrapper {
        height: 100%;

        .el-header {
            background-color: #222634;

            .title {
                color: #FFFFFF;
                font-size: 28px;
                vertical-align: middle;
                line-height: 70px;
                margin-left: 24px;
            }
        }

        .el-aside {
            .el-menu {
                height: 100%;
            }
        }

        .el-main {
            height: 100%;

            .fade-enter-active, .fade-leave-active {
                transition: opacity .7s;
            }

            .fade-enter, .fade-leave-to {
                opacity: 0;
            }

            .markdown-body {
                max-width: 852px;
                margin: 0 auto;
            }
        }

        .scrollbar {
            &::-webkit-scrollbar { /*滚动条整体样式*/
                width: 4px; /*高宽分别对应横竖滚动条的尺寸*/
                height: 4px;
            }

            &::-webkit-scrollbar-thumb { /*滚动条里面小方块*/
                border-radius: 2px;
                -webkit-box-shadow: inset 0 0 5px #222634;
                background: rgba(0, 0, 0, 0.2);
            }

            &::-webkit-scrollbar-track { /*滚动条里面轨道*/
                -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
                border-radius: 0;
                background: rgba(0, 0, 0, 0.1);
            }
        }
    }

</style>
