import Vuex from 'vuex';
import Vue from 'vue';
import API from './api.js';
import {Base64} from 'js-base64';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        repos: [
            {owner: "vuejs", repo: "vuex", name: "Vuex"},
            {owner: "vuejs", repo: "vue-router", name: "Vue Router"}
        ],
        docs: [],
        markdownMap: new Map()
    },
    mutations: {
        addDoc: function (state, doc) {
            state.docs.push(doc);
        },
        addMarkdown: function (state, markdown) {
            state.markdownMap.set(markdown.sha, markdown);
        }
    },
    actions: {
        getTree: async function ({commit}, repo) {
            const configContent = await API.getContents({...repo, path: "docs/.vuepress/config.js"})
                .then(res => Base64.decode(res.content));
            const result = eval(
                "(function a(){" +
                configContent.replace("module.exports =", "return") +
                "})()"
            );
            let tree = result.themeConfig.locales["/zh/"].sidebar;
            tree = tree.map(value => mapPath(value));
            const zhSha = await API.getContents({...repo, path: "docs"})
                .then(res => res.filter(value => value.path === 'docs/zh')[0].sha);
            const markdowns = await API.getTrees({...repo, sha: zhSha})
                .then(res => res.tree.filter(value => value.type === 'blob'))
            for (let i = 0; i < markdowns.length; i++) {
                const markdown = markdowns[i];
                markdown.title = await getTitle(repo, markdowns[i]);
                markdown.repo = repo;
                commit('addMarkdown', markdown);
            }
            tree = tree.map(value => mapMarkdown(value, markdowns));
            commit('addDoc', {children: tree, title: repo.name});
        },
        getBlobs: function ({commit}, {repo, sha}) {
            return API.getBlobs({...repo, sha})
                .then(res => Base64.decode(res.content));
        }
    }
});

function mapPath(path) {
    if (path.children) {
        path.children = path.children.map(value => mapPath(value));
        return path;
    } else if (path.endsWith('/')) {
        return {path: path + 'README.md'};
    } else {
        return {path: path.endsWith('.md') ? path : path + '.md'};
    }
}

function mapMarkdown(tree, markdowns) {
    if (tree.children) {
        tree.children = tree.children.map(value => mapMarkdown(value, markdowns));
        return tree;
    } else {
        const index = markdowns.findIndex(value => tree.path === '/zh/' + value.path);
        tree.title = index > -1 ? markdowns[index].title : '';
        tree.sha = index > -1 ? markdowns[index].sha : '';
        return tree;
    }
}

async function getTitle(repo, tree) {
    const title = await API.getBlobs({...repo, sha: tree.sha})
        .then(res => Base64.decode(res.content))
        .then(res => /#.?.+/.exec(res)[0].replace('#', ''));
    return title;
}


