import Axios from 'axios';

const instance = Axios.create({
    baseURL: 'https://api.github.com'
})

instance.interceptors.response.use(response => response.data);

function getContents({owner, repo, path}) {
    return instance.get(`/repos/${owner}/${repo}/contents/${path}`)
}

function getBlobs({owner, repo, sha}) {
    return instance.get(`/repos/${owner}/${repo}/git/blobs/${sha}`)
}

function getTrees({owner, repo, sha = 'master'}) {
    return instance.get(`/repos/${owner}/${repo}/git/trees/${sha}?recursive=1`)
}

export default {getContents, getBlobs, getTrees}
